import { useEffect, useRef, useState } from "react";
import uuid from "react-uuid";
import "./Form.css";
import axios from "axios";
function Form() {
    const [Data, setData] = useState({
        Name: "",
        Email: "",
        Phone: "",
    });
    const [Records, setRecords] = useState([]);
    const [FirstResponse, setFirstResponse] = useState([]);
    const spreaded = FirstResponse;

    const [testData, setTestData] = useState([
        {
            "Email": "rahulppagar@gmail.com",
            "Name": "Rahul Pagar",
            "Phone": "7620158967",
            "id": "01"
        },

        {
            "Email": "pagat@gmail.com",
            "Name": "rahuk",
            "Phone": "67634849",
            "id": "02"
        }

    ]);



    async function Getter() {
        const response = await axios.get("https://react-learning-ac711-default-rtdb.firebaseio.com/Records/Data.json");
        console.log("1st occ", response.data);
        console.log("This is test Data", testData);
        setTestData([...testData, FirstResponse]);
        console.log("trying to merge", testData);

    }

    async function DataPost() {
        const response = await axios.post("https://react-learning-ac711-default-rtdb.firebaseio.com/Records/Data.json", Data)
        console.log("Post Response", response);
        if (response.status == 201) {
            window.alert("Data submitted");
        }
        await Getter();


    }

    function inputHandler(e) {
        setData({ ...Data, [e.target.name]: e.target.value, id: uuid() })
        console.log(Data);
    }

    async function submitHandler(e) {
        e.preventDefault();
        console.log("From Submit",Data);
        setRecords([...Records, Data]);
        console.log(Records);
        //await DataPost();
        setData({
            Name: "",
            Email: "",
            Phone: "",
        })

    }

    // useEffect(() => {
    //     Getter();

    // }, [])


    useEffect(() => {
        console.log(Records);
        //console.log("close",FirstResponse);

    }, [Records, FirstResponse,Data])

    function deleteHandler(e){
        console.log(e); 
    const id=e.target.id;
    const newRecords=Records.filter((elem)=>{
        return elem.id!==id;
    })
    console.log(id);
    console.log("this is new record",newRecords);
    setRecords(newRecords);
    }



    return (<>
        <div id="Form-container">
            <form>

                <div>
                    <label htmlFor="Name" > User Name </label> <br />
                    <input type="text" name="Name" id="Name" onChange={inputHandler} value={Data.Name}></input>
                </div>
                <div>
                    <label htmlFor="Email" >Email </label> <br />
                    <input type="email" name="Email" id="Email" onChange={inputHandler} value={Data.Email}></input>
                </div>
                <div>
                    <label htmlFor="Phone" >Phone No.</label><br />
                    <input type="tele" name="Phone" id="Phone" onChange={inputHandler} value={Data.Phone}></input>
                </div>
                <button type="submit" onClick={submitHandler}> Submit </button>

            </form>
        </div>

        {
            Records.map((elem) => {
                const id=elem.id;
                return (
                    <div key={elem.id}>
                        <h1>{elem.Name}</h1>
                        <p>{elem.Email}</p>
                        <p>{elem.Phone}</p>
                        <button id={elem.id} onClick={deleteHandler}>Delete</button>
                        <br/>
                    </div>
                )
            })
        }
    </>
    );
}

export default Form;