import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import styles from "./Todolist.css"
import { flexbox, padding } from '@mui/system';
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import { useState,useEffect } from 'react';

export default function FullWidthTextField(props) {
// States
    const [Data,setData]=useState({
        User_Name:'',
        Email_ID:'',
        Phone_No:'',
        City:'',
    });

    const [Records,setRecords]=useState([]);
// States

//Handlers
function inputHandler(e){
    e.preventDefault();
    setData({...Data,[e.target.name]:e.target.value});
    console.log(Data);
}

function submitHandler(e){
    e.preventDefault();
setRecords([...Records,Data]);

setData({
    User_Name:'',
    Email_ID:'',
    Phone_No:'',
    City:'',
});
 //console.log(Records);
}
//Handlers

//Effects
useEffect(()=>{
console.log(Records);
props.data(Records);
},[Records])
//Effects

//Props
useEffect(()=>{
  
},[])

//Props


  return (
      <div className='container'>
          <form onSubmit={submitHandler} >
    <Box
      sx={{
          display:flexbox,
        width: 500,
        maxWidth: '100%',
        
      }}
    >
        <Typography variant="h5" sx={{marginBottom:3,textAlign:'center',}}> Employee Details Form </Typography>

      <TextField fullWidth label="User_Name" id="User_Name" name="User_Name" sx={{marginBottom:2}} onChange={inputHandler} value={Data.User_Name}/>
      <br/>
      <TextField fullWidth label="Email ID" id="User_Name" name="Email_ID" sx={{marginBottom:2}} onChange={inputHandler} value={Data.Email_ID}/>
      <br/>
      <TextField fullWidth label="Phone No" id="User_Name" name="Phone_No" sx={{marginBottom:2}} onChange={inputHandler} value={Data.Phone_No}/>
      <br/>
      <TextField fullWidth label="City" id="User_Name" name="City" sx={{marginBottom:2}} onChange={inputHandler} value={Data.City}/>
      <br/>
      <Button variant="outlined"  type="sumbit" >Submit</Button>
    </Box>
    </form>
    </div>
  );
}
